










































































































































































@import "../../theme/index";

.begda-data-panel-theme-1 {
  border: 1px @lineColor solid;
  background-color: @bgColor;
  border-radius: 5px;
}

.begda-data-panel-theme-2 {
  background-color: @bgColor;
  border-radius: 5px;
}

.header() {
  padding: 0px @paddingLeftRight;
  padding-top: 10px;
}

.header-title-line() {
  float: left;
  list-style: none;
  padding: 0;
  margin: 2px 0 0;
  display: block;
  /*height: 29px;*/

  :first-child {
    margin-left: 0;
  }

  > li {
    display: inline;
    margin-left: 8px;
    float: left;
  }

  .begda-data-panel_header-title {
    font-size: 16px;
    line-height: 27px;
    color: @titleColor;
  }

  .subhead {
    color: #000000;
    margin-top: 4px;
  }

  /*.knob {*/
  /*  background-color: #fc416d;*/
  /*  width: 5px;*/
  /*  height: 18px;*/
  /*  margin-top: 5px;*/
  /*}*/
}

.begda-data-panel {
  @paddingLeftRight: 15px;
  overflow: hidden;
  padding-bottom: 0 !important;

  .begda-data-panel_header-1 {
    padding: 0px @paddingLeftRight;
    padding-top: 10px;

    .begda-data-panel_header-title-line {
      .header-title-line();

      .knob {
        background-color: #fc416d;
        width: 5px;
        height: 18px;
        margin-top: 5px;
      }
    }
  }

  .begda-data-panel_header-2 {
    border-bottom: 1px @titleBlueColor dashed;

    .begda-data-panel_header-title-line {
      .header-title-line();

      .knob {
        background: initial !important;
        background: transparent !important;
        width: initial;
        height: initial;
        margin-top: initial;
        font-size: 22px;
        color: @titleBlueColor;
      }

      .begda-data-panel_header-title {
        color: @titleBlueColor;
        font-size: 18px;
        line-height: 27px;
      }
    }
  }

  .begda-data-panel_header-action {
    float: right;
    list-style: none;
    padding: 0;
    margin: 2px 0 0;
    display: block;
    height: 29px;

    .el-input {
      .el-input__inner {
        margin: 0;
        height: 29px;
        border-radius: 30px;
      }
    }

    .el-dropdown {
      margin-left: 10px;
    }

    button {
      padding: 5px;
      margin-left: 10px;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  .begda-data-panel_filters {
    /*padding-top: 15px;*/

    .el-form-item__label {
      font-size: 12px;
      line-height: 20px;
      padding-bottom: 2px;
    }

    .el-input__inner {
      padding: 0 5px;
    }
  }

  .begda-data-panel_tools {
    background-color: #f5faff;
    padding: 8px @paddingLeftRight;

    .el-button {
      padding: 6px 7px;
    }
  }

  .begda-data-panel__body-1 {
    padding: 0 @paddingLeftRight;
    /*overflow: hidden;*/
  }

  .begda-data-panel__body-2 {
    padding: 0;
    /*overflow: hidden;*/
  }

  .el-scrollbar__wrap {
    overflow-x: hidden;
  }

  .begda-data-panel_footer {
    border-top: 1px @lineColor solid;
    padding: 10px @paddingLeftRight;
  }
}
