











































































































.reset-progress() {
  .el-progress-bar {
    padding: 0;
    margin: 0;
    border-radius: 0;
  }
  .el-progress__text {
    display: none;
  }
  .el-progress-bar__outer {
    border-radius: 0;
  }
  .el-progress-bar__inner {
    border-radius: 0;
  }

  .el-progress {
    line-height: 0;
  }
}

.begda-progress-2 {
  .reset-progress();
  position: relative;
  .info {
    position: absolute;
    left: 0;
    top: 0;
    display: block;
    z-index: 1;
    padding-left: 5px;
    font-size: 14px;
    color: #ffffff;
  }
}
.begda-progress-1 {
  .reset-progress();
  .title {
    font-size: 25px;
    float: left;
  }
  .sub-title {
    padding-top: 10px;
    padding-left: 5px;
    float: left;
    font-size: 12px;
    color: #ffffff;
  }
  .box {
    /*padding-left: 5px;*/
    padding: 3px 5px;
    border-bottom: 1px saddlebrown solid;
    /*background: rgba(0, 0, 0, 0.4);*/
    list-style: none;
    div {
      width: 50%;
      display: inline-block;
    }
    .info {
      font-size: 14px;
      color: #ffffff;
    }
    .number {
      text-align: right;
      font-size: 14px;
      color: #ffffff;
    }
  }
}
